.container {
  display: flex;
  align-items: center;
  /* background-color: rgb(0, 0, 0); */
  column-gap: 200px;
  height: 100vh;
}
.bgimg {
  position: absolute;
  opacity: 0.4;
}
.container>div{
    z-index: 1;
}
h1{
    font-size: 40px;
}
h2{
    font-size: 30px;
}
.profileImg {
  width: 400px;
  height: auto;
  border-radius: 50%;
  z-index: 0;
}
.sec {
  z-index: 1;
  display: flex;
  color: rgb(0, 0, 0);
  flex-direction: column;
  gap: 20px;
}

@media only screen and (max-width: 800px) {
    .profileImg{
        width: 300px;
    }
}

@media only screen and (max-width: 1100px) {
    .container{
        /* column-gap: 0; */
    }
}

@media only screen and (max-width: 600px) {
  .container {
    flex-direction: column;
  }
  .profileImg {
    width: 200px;
  }
  .bgimg{
    height: 100vh;
  }
  h1{
    font-size: 25px;
}
h2{
    font-size: 20px;
}
}

/* typing anime */

.sec>div{
  font-size: 30px;
}

